@use '../../styles/breakpoint.scss';

@font-face {
  font-family: 'DFHeiStd-W9';
  src: url('../../assets/fonts/DFHeiStd-W9.otf');
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
}

@font-face {
  font-family: 'DFHeiStd-W7';
  src: url('../../assets/fonts/DFHeiStd-W7.otf');
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
}

@font-face {
  font-family: 'FuturaCom';
  src: url('../../assets/fonts/FuturaCom.ttf');
  font-weight: bold;
  font-stretch: condensed;
  font-style: normal;
}

.container {
  min-height: 100vh;
  min-width: 100vw;
  background-color: #01012e;
  position: relative;
}

.basemapImg {
  position: absolute;
  width: 100vw;
  object-fit: fill;
}

.topLogoImg {
  position: absolute;
  width: 25vw;
  object-fit: cover;
  top: 18.75vw;
  left: 11.62vw;
}

.backgroundTop {
  position: absolute;
  width: 100vw;
  object-fit: cover;
  top: 0;
  z-index: 2;
}

.backgroundBottom {
  position: absolute;
  width: 100vw;
  object-fit: cover;
  bottom: 0;
  z-index: 2;
}

.body {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: url('../../assets/images/background_middle.png');
  background-size: 100vw auto;
  background-color: #012674;

  .titleImg {
    margin-top: 62vw;
    z-index: 999;

    img {
      width: calc(87vw);
    }
  }

  .buttons {
    display: flex;
    flex-direction: row;
    margin-top: 58px;
    
    button {
      width: 35vw;
      height: 75px;
      border-radius: 37.5px;
      border: none;
      background-color: #db0840;
      cursor: pointer;
      z-index: 999;

      p {
        font-family: 'DFHeiStd-W9';
        font-size: 35px;
        line-height: 1.46;
        letter-spacing: 1.05px;
        text-align: center;
        color: #fff;
        margin: 0;
      }
    }

    button:last-child {
      margin-left: 35px;
    }
  }

  .title {
    font-family: 'DFHeiStd-W9';
    font-size: 64px;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    margin: 72.5px 0px 23px 0px;
  }

  .subtitle {
    font-family: 'DFHeiStd-W9';
    font-size: 36px;
    line-height: normal;
    letter-spacing: normal;
    text-align: center;
    color: #fff;
    display: flex;
    flex-direction: row;

    p{
      font-family: 'FuturaCom';
      font-size: 36px;
      margin: 0 12px;
    }
  }

  .mobileSubtitle {
    display: none;
  }

  .search {
    display: flex;
    align-items: center;
    margin: 57px 0px 72px 0px;
    padding: 10px 18px;
    border-radius: 37.5px;
    border: solid 3px #fff;
    width: 57vw;

    svg {
      width: 8vw;
      height: 5vw;
      margin-right: 8px;
      fill: #fff;

      path {
        fill: #fff;
      }
    }

    input {
      font-family: 'DFHeiStd-W9';
      font-size: 36px;
      line-height: normal;
      letter-spacing: normal;
      color: #fff;
      background-color: transparent;
      border: none;
      z-index: 999;
      width: 100%;
    }

    input:focus {
      outline: none;
    }
  }

  .table{
    margin-bottom: 55px;
    width: 65vw;

    .header {
      background-image: url('../../assets/images/table_header.png');
      background-size: cover;
      height: 77px;
      display: flex;
      flex-direction: row;
      align-items: center;

      .text {
        font-family: 'DFHeiStd-W7';
        font-size: 34px;
        line-height: normal;
        letter-spacing: normal;
        text-align: center;
        color: #fff;
      }

      .rank {
        flex: 1.5;
        @extend .text;
      }

      .id {
        flex: 3;
        @extend .text;
      }

      .score {
        flex: 2;
        @extend .text;
        width: 214px;
      }
    }

    .content {
      box-shadow: 20px 3px 6px 0 rgba(0, 0, 0, 0.1);
      border: solid 4px #fff;

      .cell {
        height: 78px;
        display: flex;
        flex-direction: row;
        align-items: center;
        background-image: linear-gradient(to right, rgba(255, 255, 255, 0.46) 50%, transparent 50%);
        background-size: 10px 1px;
        background-repeat: repeat-x;
        width: 100%;

        .text {
          height: 78px;
          font-family: 'FuturaCom';
          font-size: 34px;
          line-height: 2.12;
          letter-spacing: normal;
          color: #fff;
          display: flex;
          align-items: center;
          justify-content: center;
        }
  
        .rank {
          flex: 1.5;
          @extend .text;
          border-right: solid 2px #fff;
        }
  
        .id {
          flex: 3;
          @extend .text;
          border-right: solid 2px #fff;
        }
  
        .score {
          flex: 2;
          @extend .text;
          width: 214px;
        }

        .special {
          color: #F0CD4C;
        }
      }

      .cell:first-child {
        background-image: none;
      } 
    }
  }

  .bottomLogo {
    width: 30vw;
    object-fit: cover;
    margin-bottom: 100px;
  }
}

@media (max-width: breakpoint.$mobile){
  .body {
    .buttons {
      flex-direction: column;

      button {
        width: 70vw;
        height: 56px;
  
        p {
          font-size: 24px;
        }
      }
      
      button:last-child {
        margin-left: 0px;
        margin-top: 24px;
      }
    }

    .title {
      font-size: 52px;
    }

    .subtitle {
      display: none;
    }

    .mobileSubtitle {
      font-family: 'DFHeiStd-W9';
      font-size: 24px;
      line-height: normal;
      letter-spacing: normal;
      text-align: center;
      color: #fff;
      display: flex;
      flex-direction: column;
      align-items: center;

      .col {
        display: flex;
        flex-direction: row;

        p{
          font-family: 'FuturaCom';
          font-size: 24px;
          margin: 0 12px;
        }
      }
    }

    .search {
      input {
        font-size: 18px;
      }
    }
    
    .table {
      .header {
        height: 48px;

        .text {
          font-size: 16px;
        }
      }

      .content {
        .cell {
          height: 48px;

          .text {
            height: 48px;
            font-size: 16px;
          }
        }
      }
    }
  }
}

@media (min-width: breakpoint.$pc){
  .backgroundTop {
    width: 1024px;
    margin: 0 calc((100vw - 1024px) / 2);
  }
  
  .backgroundBottom {
    width: 1024px;
    margin: 0 calc((100vw - 1024px) / 2);
  }

  .basemapImg {
    width: 1024px;
    margin: 0 calc((100vw - 1024px) / 2);
  }

  .topLogoImg {
    width: 255px;
    top: 192px;
    left: calc((100vw - 1024px) / 2 + 119px);
  }

  .body {
    width: 1024px;
    margin: 0 calc((100vw - 1024px) / 2);
    background-size: 1024px auto;

    .titleImg {
      margin-top: 638px;

      img {
        width: 886px;
      }
    }

    .buttons {
      button {
        width: 336px;
      }
    }

    .search {
      width: 580px;

      svg {
        width: 54px;
        height: 54px;
      }
    }

    .table{
      width: 664px;
    }
  }
}